var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("少年泰坦")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        少年泰坦，也被称为新的青少年的Titans或简单的泰坦，是一个虚构的超级英雄团队出现在美国漫画书的出版DC漫画，往往在一个同名的月度系列。正如该组织的名字所暗示的那样，其成员是十几岁的超级英雄，其中许多人已经成为DC在正义联盟中的首映超级英雄的助手。该团队于1964年首次出现在The Brave和Bold＃54中，由Kid Flash（Wally West），Robin（Dick Grayson）和Aqualad（Garth）创立。随着Wonder Girl（Donna Troy）加入其行列，该团队在第60期采用了名为Teen Titans的名称。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        几十年来，DC多次取消并重新启动了少年泰坦，并且其页面中的各种角色都是英雄。泰坦最初四重奏的重要早期补充是Green Arrow的搭档，Speedy（Roy Harper），Aquagirl，Bumblebee，Hawk和Dove，还有三位没穿服装的英雄：拳击手Mal Duncan，通灵Lilith和穴居人Gnarrk。该系列剧首次成为真正的热门歌曲，但在20世纪80年代复兴时，作为编辑Marv Wolfman和艺术家GeorgePérez 的新青少年泰坦。这次运行描绘了原始的泰坦现在作为年轻人，并介绍了新的角色Cyborg，Starfire和Raven，以及前Doom Patrol成员Beast Boy（当时称为Changeling），他们都将成为持久的粉丝 -最爱。这个系列的重要和商业上的高点是其着名的“ 犹大合同 ”故事情节，其中团队被其成员Terra背叛其大敌丧钟。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        2000年代的故事引入了一个完全不同的Teen Titans团队，由新的DC Comics搭档组成，如新的Robin（Tim Drake），Wonder Girl（Cassie Sandsmark）和Kid Flash（Bart Allen），以及Superboy（Kon- El），其中一些人此前曾在类似的名称Young Justice中出现过。后来这个时代的显着增加包括火星小姐，掠夺者（玫瑰威尔逊），超女（Kara Zor-El）和蓝甲虫（Jaime Reyes）。同时，DC还出版了泰坦现在作为成年人的一些原始和20世纪80年代的成员，由迪克格雷森在他的成人角色Nightwing领导。后来，随着DC的一个新的运行新52在2011年重新启动了新的字符成立名册，包括冬至，邦克（米格尔·何塞·巴拉甘）和飞掠（席琳帕特森），虽然这种新的卷证明商业和批判令人失望的DC。2016年，DC使用Titans Hunt和DC Rebirth故事情节重新建立了该组织的原始创始成员和历史，将这些经典英雄重新统一为泰坦，同时引入新罗宾领导的新一代少年泰坦（